@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&family=Roboto:ital@1&display=swap);
body, .App {
  margin: 0;
  font-family: 'Noto Sans JP', sans-serif  'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  line-height: 1.4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* greeting styles  */

h1, h2, h3 {
  font-family: 'Roboto', sans-serif;
}
h1{
  font-size: 3em;
}

h2{
  font-size: 2em;
}

h3 {
  font-size: 1.4em;
}
/* intro styles */
.intro-portrait{
  border-radius: 5px;
  margin: 15px
}
p{
  max-width: 90%;
  margin: 10px auto;
}

ul {
  list-style: none;
  padding-left: 0;
  max-width: 90%; 
  margin: auto;
  text-align: center;
}

li {
  text-align: center;
  margin-bottom: 15px;
}

.about-section{
  margin: 30px 0;
  padding: 40px 0;
  background: rgb(255,255,255);
  background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(197,216,238,1) 13%, rgba(81,160,252,1) 57%, rgba(197,216,238,1) 90%, rgba(255,255,255,1) 100%);
}


.about-personal-section, .about-tech-section {
  max-width: 90%;
  margin: 20px auto;
  padding: 10px 20px;
  border-radius: 10px;
  color: black;
  background-color: #f0f8ff9c;
}
.project-sections {
  margin-bottom: 50px;
}

.projects-group > section {
  margin: 50px auto;
  box-shadow: 0 2px 10px rgb(0, 0, 0, 0.2);
  border-radius: 20px;
  max-width: 90%;
  padding-top: 10px;
  padding-bottom: 20px;
}

.current-projects {
  margin-top: 45px;
}

.contact {
  margin-top: 20px;
  margin-bottom: 40px;
}

.social-links {
  margin: 30px;
}
.a-button-div {
  margin: 20px auto;
  background-color: #5B84B1FF;
  padding: 10px;
  max-width: 200px;
  border-radius: 20px;
}

.a-button {
  color: white;
  text-decoration: none;
}

.social-links > a{
  margin: 10px;
  padding: 10px
}

footer {
  background-color: #bc5c45;
  padding: 20px;
  color: white;
}


@media all and (min-width: 520px) {
  .about-personal-section, .about-tech-section {
    max-width: 500px;
  }

  .projects-group > section {
    max-width: 500px;
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media all and (min-width: 900px) {
  .about-tech-section {
    max-width: 250px;
  }
  .about-fullstack-section {
    display: flex;
    flex-direction: row;
    margin: auto;
    justify-content: space-between;
    max-width: 1000px;
  }
  p{
    max-width: 600px;
  }
  .projects-group{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: auto 50px;
  }
  .projects-group > section {
    margin-left: 10px;
    margin-right: 10px;
  }

}
/* nav bar */
.Header{
  /* header container */
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  position: relative;
  margin-bottom: 20px;
  padding: 10px 0;
}
/*logo*/
#logo {
  margin-left: 30px;
  padding: 10px;
}

.hide {
  display: none;
}

.menu-group {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: -60px; 
  width: 100vw;
  height: 100vh;
  position: relative;
  z-index: 50;
}

.nav ul{
  margin: 2em auto;
  padding: 1em;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 30vh;
}

.nav li a{
  font-size: 2em;
  color: black;
  margin: 1 auto;
  padding: .3em 1em;
  border-radius: 15px;
  transition-duration: 100ms;
}

.nav li a:hover{
  color: white;
  background-color: #5B84B1FF;
}

.hamburger-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 50px;
  width: 50px;
  justify-content: space-evenly;
  transition-duration: 300ms ;
  padding: 15px;
  position: relative;
  
}
.hamburger-menu:hover{
  transform: scale(1.15);
  cursor: pointer;
}

.rotateUp{
  transform: rotateZ(45deg);
  position: absolute;
  right: 15px;
}
.rotateDown{
  transform: rotateZ(-45deg);
  position: absolute;
  right: 15px;
}

.hamburger-menu span{
  width: 50px;
  height: 2px;
  border: 1px solid black;
  background-color: black;
  transition-duration: 200ms;
}
#username{
  font-size: 2em;
}

@media all and (max-width: 650px) {
  .desktop-menu-group{
    display: none;
  }
}

@media all and (min-width: 650px) {
  #dropdown-menu, .hamburger-menu{
     display: none;
  }
  .Header{
    max-height: 140px;
    max-width: 900px;
  }
  .nav {
    flex-direction: row;
    margin-bottom: 0;
    width: 500px;
    height: 150px
  }
  .nav  li {
    list-style-type: none;
  }
}

@media all and (min-width: 950px) {
  .Header{
    width: 900px;
    margin: auto;
  }
}
